/* helper functions to feed into seasonal data */

/* Calculate seasonal average temperature
 */
function seasonalAverage(records, year, season) {
  const seasonMonths = {
    winter: [12, 1, 2],
    spring: [3, 4, 5],
    summer: [6, 7, 8],
    fall: [9, 10, 11],
  };

  const seasonValues = records
    .filter((r) => {
      if (r.month === 12 && season === 'winter') return r.year === year - 1;
      return r.year === year && seasonMonths[season].includes(r.month);
    })
    .map((r) => r.value);

  if (!seasonValues.length) return null;

  return seasonValues.reduce((a, b) => a + b, 0) / seasonValues.length;
}

/* Calculate seasonal average temperature across multiple years (1895-1925 baseline)
 */
function seasonalMultiYearAverage(records, startYear, endYear, season) {
  const seasonMonths = {
    winter: [12, 1, 2],
    spring: [3, 4, 5],
    summer: [6, 7, 8],
    fall: [9, 10, 11],
  };

  const seasonValues = [];

  // Collect data for all years in the range
  for (let year = startYear; year <= endYear; year++) {
    const yearRecords = records.filter((r) => {
      if (r.month === 12 && season === 'winter') return r.year === year - 1;
      return r.year === year && seasonMonths[season].includes(r.month);
    });

    seasonValues.push(...yearRecords.map((r) => r.value));
  }

  if (!seasonValues.length) return null;

  return seasonValues.reduce((a, b) => a + b, 0) / seasonValues.length;
}

/* Get alternative state data for comparison */
function getAlternativeStateData(
  season,
  birthYear,
  currentState,
  currentTempAtBirth,
  currentTempNow
) {
  // Find the state with the greatest temperature change for this season
  // Only show alternative if a different state has greater change than current state

  let greatestChangeState = null;
  let greatestChange = 0;
  let currentStateChange = Math.abs(currentTempNow - currentTempAtBirth);

  // First pass: Find state with greatest change (including current state)
  Object.keys(climate).forEach((state) => {
    if (!climate[state]) return;

    const stateData = climate[state];
    // Use tavg for temperature calculations
    const tempData = stateData.tavg;
    const tempAtBirth = seasonalAverage(tempData, birthYear, season);
    const tempNow = seasonalAverage(tempData, 2025, season);

    if (tempAtBirth !== null && tempNow !== null) {
      const change = Math.abs(tempNow - tempAtBirth);
      if (change > greatestChange) {
        greatestChange = change;
        greatestChangeState = state;
      }
    }
  });

  // Only return alternative if a DIFFERENT state has greater change than current state
  if (
    greatestChangeState &&
    greatestChangeState !== currentState &&
    greatestChange > currentStateChange
  ) {
    const stateData = climate[greatestChangeState];
    // Use tavg for temperature calculations
    const tempData = stateData.tavg;
    return {
      state: greatestChangeState,
      avgAtBirth: seasonalAverage(tempData, birthYear, season),
      avgNow: seasonalAverage(tempData, 2025, season),
      minAtBirth: seasonalAverage(stateData.tmin, birthYear, season),
      minNow: seasonalAverage(stateData.tmin, 2025, season),
      temperatureChange: greatestChange,
    };
  }

  // If current state has greatest change, or no greater change found, return null
  return null;
}

/* Get alternative state data comparing 1925 to birth year (for readers born after 2005) */
function getAlternativeStateData1925(
  season,
  birthYear,
  currentState,
  currentTempAtBirth,
  currentTemp1925
) {
  // Find the state with the greatest temperature change from 1925 to birth year
  // Only show alternative if a different state has greater change than current state

  let greatestChangeState = null;
  let greatestChange = 0;
  let currentStateChange = Math.abs(currentTempAtBirth - currentTemp1925);

  // First pass: Find state with greatest change from 1925 to birth year (including current state)
  Object.keys(climate).forEach((state) => {
    if (!climate[state]) return;

    const stateData = climate[state];
    // Use tavg for temperature calculations
    const tempData = stateData.tavg;
    const tempAtBirth = seasonalAverage(tempData, birthYear, season);
    const temp1925 = seasonalMultiYearAverage(tempData, 1925, 1925, season); // Single year 1925

    if (tempAtBirth !== null && temp1925 !== null) {
      const change = Math.abs(tempAtBirth - temp1925);
      if (change > greatestChange) {
        greatestChange = change;
        greatestChangeState = state;
      }
    }
  });

  // Only return alternative if a DIFFERENT state has greater change than current state
  if (
    greatestChangeState &&
    greatestChangeState !== currentState &&
    greatestChange > currentStateChange
  ) {
    const stateData = climate[greatestChangeState];
    // Use tavg for temperature calculations
    const tempData = stateData.tavg;
    return {
      state: greatestChangeState,
      avgAtBirth: seasonalAverage(tempData, birthYear, season),
      avg1925: seasonalMultiYearAverage(tempData, 1925, 1925, season),
      minAtBirth: seasonalAverage(stateData.tmin, birthYear, season),
      min1925: seasonalMultiYearAverage(stateData.tmin, 1925, 1925, season),
      temperatureChange: greatestChange,
    };
  }

  // If current state has greatest change, or no greater change found, return null
  return null;
}

/* Get alternative state data for winter comparison (uses min temperatures) */
function getAlternativeStateDataWinter(
  season,
  birthYear,
  currentState,
  currentMinAtBirth,
  currentMinNow
) {
  // Find the state with the greatest WARMING change for winter
  // Only show alternative if a different state has greater warming than current state

  let greatestWarmingState = null;
  let greatestWarmingChange = 0;
  let currentStateWarmingChange = currentMinNow - currentMinAtBirth;

  // First pass: Find state with greatest warming change (including current state)
  // Only consider states that actually warmed (minNow > minAtBirth)
  Object.keys(climate).forEach((state) => {
    if (!climate[state] || !climate[state].tmin) return;

    const stateData = climate[state];
    const minAtBirth = seasonalAverage(stateData.tmin, birthYear, season);
    const minNow = seasonalAverage(stateData.tmin, 2025, season);

    if (minAtBirth !== null && minNow !== null) {
      const warmingChange = minNow - minAtBirth; // Positive = warming, Negative = cooling
      if (warmingChange > greatestWarmingChange) {
        greatestWarmingChange = warmingChange;
        greatestWarmingState = state;
      }
    }
  });

  // Only return alternative if a DIFFERENT state has greater warming than current state
  // Focus on warming trends (greatestWarmingChange > 0)
  if (
    greatestWarmingState &&
    greatestWarmingState !== currentState &&
    greatestWarmingChange > currentStateWarmingChange
  ) {
    const stateData = climate[greatestWarmingState];
    return {
      state: greatestWarmingState,
      minAtBirth: seasonalAverage(stateData.tmin, birthYear, season),
      minNow: seasonalAverage(stateData.tmin, 2025, season),
      temperatureChange: greatestWarmingChange,
    };
  }

  // If current state has greatest warming, or no greater warming found, return null
  return null;
}

/* Get alternative state data for winter comparing 1925 to birth year (for readers born after 2005) */
function getAlternativeStateDataWinter1925(
  season,
  birthYear,
  currentState,
  currentMinAtBirth,
  currentMin1925
) {
  // Find the state with the greatest WARMING change from 1925 to birth year
  // Only show alternative if a different state has greater warming than current state

  let greatestWarmingState = null;
  let greatestWarmingChange = 0;
  let currentStateWarmingChange = currentMinAtBirth - currentMin1925;

  // First pass: Find state with greatest warming change from 1925 to birth year (including current state)
  Object.keys(climate).forEach((state) => {
    if (!climate[state] || !climate[state].tmin) return;

    const stateData = climate[state];
    const minAtBirth = seasonalAverage(stateData.tmin, birthYear, season);
    const min1925 = seasonalMultiYearAverage(
      stateData.tmin,
      1925,
      1925,
      season
    );

    if (minAtBirth !== null && min1925 !== null) {
      const warmingChange = minAtBirth - min1925; // Positive = warming from 1925 to birth year
      if (warmingChange > greatestWarmingChange) {
        greatestWarmingChange = warmingChange;
        greatestWarmingState = state;
      }
    }
  });

  // Only return alternative if a DIFFERENT state has greater warming than current state
  if (
    greatestWarmingState &&
    greatestWarmingState !== currentState &&
    greatestWarmingChange > currentStateWarmingChange
  ) {
    const stateData = climate[greatestWarmingState];
    return {
      state: greatestWarmingState,
      minAtBirth: seasonalAverage(stateData.tmin, birthYear, season),
      min1925: seasonalMultiYearAverage(stateData.tmin, 1925, 1925, season),
      temperatureChange: greatestWarmingChange,
    };
  }

  // If current state has greatest warming, or no greater warming found, return null
  return null;
}

/**
 * Generic function to get extreme day projections for specific ages
 */
function getExtremeDaysByAge(
  state,
  birthYear,
  dataset,
  dataKey,
  targetAges = [60, 100],
  targetYears = []
) {
  const stateData = dataset[state];
  if (!stateData) {
    return null;
  }

  const currentYear = new Date().getFullYear();
  const currentAge = currentYear - birthYear;

  function getDataForAge(age) {
    const targetYear = birthYear + age;

    if (targetYear <= 2022) {
      return { dataYear: 2005, days: stateData[dataKey]['2005'] };
    } else if (targetYear <= 2049) {
      return { dataYear: 2039, days: stateData[dataKey]['2039'] };
    } else if (targetYear <= 2079) {
      return { dataYear: 2059, days: stateData[dataKey]['2059'] };
    } else {
      return { dataYear: 2099, days: stateData[dataKey]['2099'] };
    }
  }

  const milestones = [];

  for (const age of targetAges) {
    if (currentAge < age) {
      //person hasn't reach the age yet
      const data = getDataForAge(age);
      milestones.push({
        age: age,
        year: birthYear + age,
        days: data.days,
        dataYear: data.dataYear,
      });
    }
  }
  for (const year of targetYears) {
    if (year > currentYear) {
      let age = year - birthYear;
      const data = getDataForAge(age);
      milestones.push({
        age: age,
        year: year,
        days: data.days,
        dataYear: data.dataYear,
        isEndOfCentury: true,
      });
    }
  }

  return {
    state: state,
    birthYear: birthYear,
    currentAge: currentAge,
    milestones: milestones,
  };
}

function getDataForAgeInternal(dataset, dataKey, state, birthYear, age) {
  const stateData = dataset[state]?.[dataKey];
  if (!stateData) return null;

  const targetYear = birthYear + age;

  if (targetYear <= 2022) return stateData['2005'];
  if (targetYear <= 2049) return stateData['2039'];
  if (targetYear <= 2079) return stateData['2059'];
  return stateData['2099'];
}

/**
 * Get current extreme days for comparison
 * @param {string} state - State name
 * @param {object} dataset - The data object (extremeHeatData or extremeColdData)
 * @param {string} dataKey - The key in the data object ('extreme_heat_days' or 'extreme_cold_days')
 * @returns {number} Current extreme days (using 2005 as baseline)
 */
function getCurrentExtremeDays(state, dataset, dataKey) {
  const stateData = dataset[state];
  if (!stateData) {
    return null;
  }

  // Always use 2005 data as the current baseline
  return stateData[dataKey]['2039'];
}

/**
 * Get past extreme days (always uses 2005 data)
 * @param {string} state - State name
 * @param {object} dataset - The data object (extremeHeatData or extremeColdData)
 * @param {string} dataKey - The key in the data object ('extreme_heat_days' or 'extreme_cold_days')
 * @returns {number} Past extreme days (always using 2005 data)
 */
function getPastExtremeDays(state, dataset, dataKey) {
  const stateData = dataset[state];
  if (!stateData) {
    return null;
  }

  // Always use 2005 data for past comparison
  return stateData[dataKey]['2005'];
}

// Convenience wrapper functions for heat days
function getHeatDaysByAge(
  state,
  birthYear,
  targetAges = [60, 100],
  targetYears = [2099]
) {
  return getExtremeDaysByAge(
    state,
    birthYear,
    extremeHeatData,
    'extreme_heat_days',
    targetAges,
    targetYears
  );
}

function getCurrentHeatDays(state) {
  return getCurrentExtremeDays(state, extremeHeatData, 'extreme_heat_days');
}

function getHotPast(state) {
  return getPastExtremeDays(state, extremeHeatData, 'extreme_heat_days');
}

// Convenience wrapper functions for cold days
function getColdDaysByAge(
  state,
  birthYear,
  targetAges = [60, 100],
  targetYears = [2099]
) {
  return getExtremeDaysByAge(
    state,
    birthYear,
    extremeColdData,
    'extreme_cold_days',
    targetAges,
    targetYears
  );
}

function getCurrentColdDays(state) {
  return getCurrentExtremeDays(state, extremeColdData, 'extreme_cold_days');
}

function getFreezingPast(state) {
  return getPastExtremeDays(state, extremeColdData, 'extreme_cold_days');
}

/* Get season timing changes for a state
 * @param {string} state - State name
 * @returns {object} Season timing data
 */
function getSeasonTiming(state) {
  const stateData = seasonTiming[state];
  if (!stateData) {
    return null;
  }

  return {
    startWinter: stateData.startWinter,
    startSpring: stateData.startSpring,
  };
}

function getFreezeOrHot(state) {
  const coldData = extremeColdData[state]?.extreme_cold_days;
  const heatData = extremeHeatData[state]?.extreme_heat_days;

  const freezes = coldData
    ? Object.values(coldData).some((days) => days > 0) // any year > 0
      ? 'yes'
      : 'no'
    : 'no';

  const hot = heatData
    ? Object.values(heatData).some((days) => days > 0)
      ? 'yes'
      : 'no'
    : 'no';

  return { freezes, hot };
}
