        <h3>Data Sources</h3>
        <p>
          This analysis is based on climate projections and historical data from
          the following sources:
        </p>
        <p>
          NOAA National Centers for Environmental Information for climate
          data and temperature records, EPA Climate Indicators for Seasonal timing and frost data,
University of Chicago Climate Impact Lab for Extreme heat and cold
          day projections, and the Japan Meteoroogical Agency for data on Japan.
        </p>
        <p>
          Projections represent mid-range climate scenarios and actual outcomes
          may vary. The 1925 average temperature is based on a 30-year seasonal average
          from 1895-1925.
        </p>
